import { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { useAuthStore } from '@/store/useAuthStore';
import api from '@/lib/api';
import { LogIn, Mail, Lock, Eye, EyeOff, RefreshCw, Sparkles } from 'lucide-react';

export function LoginV2() {
  const navigate = useNavigate();
  const { toast } = useToast();
  const { setAuth } = useAuthStore();
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  });
  const [showPassword, setShowPassword] = useState(false);
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.email.trim()) {
      toast({
        title: 'خطأ',
        description: 'أدخل البريد الإلكتروني',
        variant: 'destructive',
      });
      return;
    }

    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      toast({
        title: 'خطأ',
        description: 'صيغة البريد الإلكتروني غير صحيحة',
        variant: 'destructive',
      });
      return;
    }

    if (!formData.password) {
      toast({
        title: 'خطأ',
        description: 'أدخل كلمة المرور',
        variant: 'destructive',
      });
      return;
    }

    setLoading(true);
    try {
      const response = await api.post('/auth/login', formData);
      const { user, token } = response.data;
      
      // Set auth first
      setAuth(user, token);
      
      // Set token in axios defaults
      api.defaults.headers.common['Authorization'] = `Bearer ${token}`;
      
      toast({
        title: 'نجح',
        description: 'تم تسجيل الدخول بنجاح',
        variant: 'success',
      });
      
      // Small delay to ensure state is updated
      setTimeout(() => {
        navigate('/dashboard', { replace: true });
      }, 100);
    } catch (error: any) {
      toast({
        title: 'خطأ',
        description:
          error.response?.data?.error ||
          error.code === 'ERR_NETWORK'
            ? 'لا يمكن الاتصال بالخادم'
            : 'حدث خطأ أثناء تسجيل الدخول',
        variant: 'destructive',
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center relative overflow-hidden" style={{ background: 'linear-gradient(135deg, #BBBCC0 0%, #e8e9eb 100%)' }}>
      {/* Subtle Background Pattern */}
      <div className="absolute inset-0 overflow-hidden opacity-20">
        <div className="absolute top-0 right-0 w-96 h-96 rounded-full blur-3xl" style={{ background: '#068294' }} />
        <div className="absolute bottom-0 left-0 w-96 h-96 rounded-full blur-3xl" style={{ background: '#2A6E85' }} />
      </div>

      {/* Main Content */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="w-full max-w-2xl z-10 px-4"
      >
        <Card className="shadow-2xl" style={{ border: '2px solid #2A6E85', background: '#ffffff', padding: '2rem 0' }}>
          <CardHeader className="text-center space-y-4 pb-6 pt-8 px-8">
            <motion.div
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              transition={{ delay: 0.2, type: "spring", stiffness: 200 }}
              className="mx-auto mb-8 flex justify-center items-center w-full"
            >
              <img 
                src="/logo.png" 
                alt="ALSAQI Logo" 
                style={{ 
                  width: '450px', 
                  maxWidth: '80%',
                  height: 'auto', 
                  margin: '0 auto', 
                  display: 'block',
                  objectFit: 'contain'
                }}
                onError={(e) => {
                  const target = e.target as HTMLImageElement;
                  console.error('Failed to load logo:', target.src);
                  target.style.display = 'none';
                }}
              />
            </motion.div>
              <CardTitle className="text-3xl font-bold" style={{ color: '#1a1a1a' }}>
                تسجيل الدخول
              </CardTitle>
              <div className="space-y-1">
                <p className="text-lg font-semibold" style={{ color: '#1a1a1a' }}>نظام إدارة المهام والحضور</p>
                <p className="text-sm" style={{ color: '#444', fontWeight: 500 }}>قسم التسويات والمطابقة</p>
              <p className="text-xs font-semibold mt-2" style={{ color: '#068294' }}>شركة الصاقي للدفع الإلكتروني</p>
            </div>
            <Link
              to="/quick-login"
              className="inline-flex items-center gap-2 text-sm font-medium transition-colors mt-2"
              style={{ color: '#068294' }}
            >
              <Sparkles className="h-4 w-4" />
              <span>تسجيل دخول سريع للاختبار</span>
            </Link>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-5">
              <motion.div
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.3 }}
                className="space-y-2"
              >
                <label className="text-sm font-semibold" style={{ color: '#1a1a1a' }}>البريد الإلكتروني</label>
                <div className="relative">
                  <Mail className="absolute right-3 top-1/2 -translate-y-1/2 h-5 w-5" style={{ color: '#2A6E85' }} />
                  <Input
                    type="email"
                    value={formData.email}
                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                    placeholder="أدخل بريدك الإلكتروني"
                    className="pr-10 h-12 text-base transition-colors"
                    style={{ 
                      border: '2px solid #2A6E85', 
                      color: '#2A6E85',
                      background: '#ffffff'
                    }}
                    required
                  />
                </div>
              </motion.div>

              <motion.div
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.4 }}
                className="space-y-2"
              >
                <label className="text-sm font-semibold" style={{ color: '#1a1a1a' }}>كلمة المرور</label>
                <div className="relative">
                  <Lock className="absolute right-3 top-1/2 -translate-y-1/2 h-5 w-5" style={{ color: '#2A6E85' }} />
                  <Input
                    type={showPassword ? 'text' : 'password'}
                    value={formData.password}
                    onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                    placeholder="أدخل كلمة المرور"
                    className="pr-10 pl-12 h-12 text-base transition-colors"
                    style={{ 
                      border: '2px solid #2A6E85', 
                      color: '#2A6E85',
                      background: '#ffffff'
                    }}
                    required
                  />
                  <Button
                    type="button"
                    variant="ghost"
                    size="icon"
                    className="absolute left-2 top-1/2 -translate-y-1/2 h-8 w-8 hover:bg-transparent"
                    onClick={() => setShowPassword(!showPassword)}
                  >
                    {showPassword ? (
                      <EyeOff className="h-5 w-5" style={{ color: '#2A6E85' }} />
                    ) : (
                      <Eye className="h-5 w-5" style={{ color: '#2A6E85' }} />
                    )}
                  </Button>
                </div>
              </motion.div>

              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.5 }}
              >
                <Button
                  type="submit"
                  className="w-full h-12 text-base font-bold text-white shadow-lg hover:shadow-xl transition-all duration-300"
                  style={{ 
                    background: '#068294',
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.background = '#2A6E85';
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.background = '#068294';
                  }}
                  disabled={loading}
                >
                  {loading ? (
                    <>
                      <RefreshCw className="h-5 w-5 ml-2 animate-spin" />
                      جاري تسجيل الدخول...
                    </>
                  ) : (
                    <>
                      <LogIn className="h-5 w-5 ml-2" />
                      تسجيل الدخول
                    </>
                  )}
                </Button>
              </motion.div>
            </form>
          </CardContent>
        </Card>
      </motion.div>
    </div>
  );
}
